import React, { useState } from "react"
import styled from "styled-components"
import { Link } from 'react-router-dom'
import { useWallet } from "@binance-chain/bsc-use-wallet"
import Container from "components/layout/Container"
import UnlockButton from "components/UnlockButton"
import { Flex, Heading, Button, Spinner } from "leek-uikit"
import Hero from "./Hero"
import CollectionCard from "./CollectionCard"
import useGetAllCollections from "./api"
import HowToGenerate from "./HowToGenerate"

const CollectionsLayout = styled.div`
  display: grid;
  grid-gap: 32px;
  grid-template-columns: 1fr;
  padding-left: 50px;
  padding-right: 50px;
  padding-top:50px;
  padding-bottom:50px;

  ${({ theme }) => theme.mediaQueries.md} {
    grid-template-columns: repeat(2, 1fr);
  }

  ${({ theme }) => theme.mediaQueries.xl} {
    grid-template-columns: repeat(3, 1fr);
  }

@media (max-width: 768px) {
   padding:30px
  }
`

const Wrapper = styled(Container)`
  display: flex;
  justify-content: center;
  align-items: center;
  padding:100px;
`

const Collections = () => {
    const { account } = useWallet()
    const collections = useGetAllCollections(account)

    let comp

    if (account) {
        if (collections) {
            if (collections.length > 0) {
                comp = <CollectionsLayout>
                    {
                        collections.map(collection => (
                            <CollectionCard key={collection.id} collection={collection} />
                        ))
                    }
                </CollectionsLayout>

            } else {
                comp = <Container >
                    <Flex width="100%" flexDirection="column" alignItems="center" justifyContent="center">
                        <Heading size="xl" color="secondary" mt="40px" mb="40px" fontSize="50px">
                            Generate Your Collections ðŸ˜Š
                        </Heading>
                    </Flex>
                    <Flex alignItems="center" justifyContent="center" mt="20px">
                        <Button as={Link} to="/billboard">Map Billboard</Button>
                    </Flex>
                </Container >
            }
        } else {
            comp = <Container>
                <Wrapper>
                    <Spinner />
                </Wrapper>
            </Container>
        }
    } else {
        comp = <Container>
            <Flex width="100%" flexDirection="column" alignItems="center" justifyContent="center">
                <Heading size="xl" color="secondary" mt="40px" mb="40px" fontSize="50px">
                    Unlock Wallet to View Collections
                </Heading>
            </Flex>
            <Flex alignItems="center" justifyContent="center" mt="20px">
                <UnlockButton />
            </Flex>
        </Container>
    }

    return (
        <div>
            <Hero />
            {comp}
            <Container>
                <HowToGenerate />
            </Container>


        </div>
    )
}

export default Collections
