import { useEffect, useState } from "react"
import axios from "axios"
import { useBillboardCollectionContract } from "hooks/useContract"

const useGetAllCollections = (account: string) => {
    const [collections, setCollections] = useState(null)
    const contract = useBillboardCollectionContract()

    useEffect(() => {
        let mounted = true;
        const fetchData = async () => {
            try {
                if (account) {
                    const traits = [];

                    const tokensOfOwner = await contract.methods.tokensOfOwner(account).call()

                    await Promise.all(tokensOfOwner.map(async id => {
                        const hash = await contract.methods.tokenURI(id).call()
                        const { data } = await axios.get(hash)
                        traits.push({
                            id,
                            ...data
                        })
                    }))

                    if (mounted) {
                        setCollections(traits)
                    }

                }
            } catch (err) {
                console.error("Unable to fetch collections data", err.response)
            }
        }
        fetchData();
        return () => { mounted = false }
    }, [account])// eslint-disable-line react-hooks/exhaustive-deps
    return collections
}

export default useGetAllCollections
